%cover {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

.cover {

	overflow: hidden;
	z-index: 1;

	@extend %cover;

	video {
		z-index: 2;
	}

	img,
	video {
		@extend %cover;
		object-fit: cover;
	}

	img {
		object-position: top;
	}

	&.cover--overlay {
		&::after {
			content: '';
			display: block;
			@extend %cover;
			background: rgba(0, 0, 0, .88);
			z-index: 3;
		}    
		
		&.overlay--blue {
			&:after {
				background: rgba(0, 69, 230, .8);
			}
			img,
			video {
				filter: grayscale(100%);
			}
		}
	}

}