.content__main {

    &:not(.content--no-margin) {
        margin-bottom: 4rem;

        @media (max-width: $breakpoint_mobile) {
            margin-bottom: 2rem;
        }  
    }

    &:not(.content--has-banner) {
        padding-top: 1rem;

        > .breadcrumbs {
            margin-top: 0;
        }
    }

}

.content__title {
    margin: 2rem 0;
    h1 {
        font-weight: 600;
        font-size: 3.5em;
        margin: 0 auto;
        max-width: 40rem;

        &::after {
            content: '';
            display: block;
            width: 4rem;
            height: .5rem;
            background: $blue;
            margin: 2rem auto;
        }
    }

    @media (max-width: $breakpoint_mobile) {
        h1 {
            font-size: 2.5em;
        }
    }
}


.content__section {
    position: relative;
    margin: 4rem 0;

    &.section--home {
        @media (min-width: $breakpoint_mobile + 1px) {
            margin: 6rem 0;
        }
        @media (max-width: $breakpoint_mobile) {
            margin: 4rem 0;
        }         
    }

  

    &.section--no-margin {
        margin: 0;
    }
    &.section--banner {
        .section__header {
            h2 {
                @media (min-width: $breakpoint_hd) {
                    font-size: 3em;
                }
            }
        }
    }

    @media (max-width: $breakpoint_mobile) {
        margin: 2rem 0;
    }      


}

.section__header {
    
    h2 {
        font-size: 3em;
        max-width: 45rem;

        &:last-child {
            margin: 0;
        }
    }
    h3 {
        font-size: 1.5em;
        font-weight: 500;
        margin-bottom: 1rem;
    }
    p {
        font-size: 1.125em;
        line-height: 2;
        max-width: 40rem;
        
        &:not(:last-child) {
            margin-bottom: 2rem;
        }

        a {
            text-decoration: underline;
        }

    }
    &.text--center {
        h2 {
            max-width: 55rem;
        }
        h2,
        p {
            margin-left: auto;
            margin-right: auto;
        }
    }

    &:not(.header--no-margin) {
        margin-bottom: 6rem;
        @media (max-width: $breakpoint_mobile) {
            margin-bottom: 4rem;
        }
    }

    @media (max-width: $breakpoint_mobile) {
        h2 {
            font-size: 2em;
            margin-bottom: 1rem;
        }
    }

}

.section__body {
    position: relative;
    z-index: 2; //above cover
}

.section__cta {
    margin: 2rem 0;
}
