//default style of generic html elements

body {
    font-family: 'Inter', sans-serif;
    font-size: 1em;
    font-weight: 300;
    color: $text_silver;
    overflow-x: hidden;
}


a {
    color: $black;
    text-decoration: none;

    @media (hover: hover) {
        &:hover {
            text-decoration: underline;
        }
    }
}

ul {
    list-style-type: none
}


img {
    width: 100%;
    height: auto; //takes care of inline width/height attributes
    vertical-align: bottom;
}


address {
    font-style: normal;
}

button {
    cursor: pointer;
    background: transparent;
}

figcaption {
    margin-top: .5rem;
    font-style: italic;
    color: silver;
}

dl {
    dt {
        float: left;
        margin-right: 1rem;
    }
}
