.video-component {

	position: relative;

	display: flex;
	align-items: center;
	justify-content: center;

	background: #000;


	.video__container {
		position: absolute;
		top: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	.video__poster {
		
		z-index: 2;
		text-align: center;

		transition: opacity .2s;
	}

	.video__desc {

		width: 100%;
		display: flex;
		align-items: center;

		padding: 2rem 1rem;
		position: relative;
		z-index: 4; //above cover
		
	}

	.video__button {
		margin-bottom: 0;
	}

    .button__label {
        display: block;
        color: #fff;
        font-size: 2em;
        font-weight: 400;	
        max-width: 30rem;
        margin: 0 auto 1rem;		
    }


	@media (min-width: $breakpoint_mobile + 1px) {
		height: 36rem;
		will-change: height;
		transition: height .4s ease-in-out;
	}
	@media (max-width: $breakpoint_mobile) {
		
		
		padding-bottom: 56.25%;

		.video__poster {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;

			display: flex;
			align: center;
		}

		.video__desc {

		}
		
        .button__label {
            font-size: 2em;
        }
		
		.video__index {
			display: none;
		}
    }	
    
    @media (max-width: $breakpoint_phone-big) {
        .button__label {
            font-size: 1.375em;
        }
    }


	//player state managed via YouTube iFrame API, see js/player.js
    &[data-state='started'],
    &[data-state='playing'] {

		@media (min-width: $breakpoint_mobile) {
			height: 90vh;
			//padding-bottom: 56.25%;
		}

        .video__poster {
            //visibility: hidden;
            opacity: 0;
        }
        .video__container {
        	z-index: 10;
        }
    }
    &[data-state='playing'] {
   
    }
    &[data-state='paused'] {

    }
    &[data-state='ended'] {

    }

}
//video__button reused in components/banner/index.twig
.video__button {
	display: inline-flex;
	align-items: center;
	text-align: left;
	margin-bottom: 2rem;
	color: #fff;

	.ui-ico {
		height: 4rem;
		margin-right: 1.5rem;
		transition: transform .2s;		
	}
	p {
		font-size: 1.25em;
		font-weight: bold;
		margin-bottom: .5rem;
		line-height: 1;
	}
	span {
		font-size: 1em;
	}

	@media (hover: hover) {
		&:hover {
			.ui-ico {
				transform: scale(1.1);
			}
		}
	}

} 