/** Top Navigation embedded in views/layout/header; markup generated via wp_nav **/

.nav--top {
    
    a {
        display: block;
        font-weight: 600; //semi-bold
        font-size: 1.125em;
    }

    ul {
        display: none;
    }

    > ul {
        display: block;
    }

    .item--has-submenu {
        > a {
            display: flex;
            align-items: center;
            justify-content: center;

            &:after {
                margin-left: .5rem;
                @include submenu-indicator(.25rem, #aaa);
            }                 
        }

        &.menu--open {
            > a {
                &:after {
                    transform: rotate(180deg);
                }
            }            
            .nav__submenu {
                display: block;
            }
        }
    }

    @media (min-width: $breakpoint_mobile + 1px) {

        > ul {

            > li {
                display: inline-block;
                margin: 0 .5rem;

                @media (max-width: $breakpoint_desktop) {
                    margin: 0 .25rem;
                }
                > a {
                   padding: 1rem 0;
                }

                > ul {
                    > .submenu__layout {
                        display: grid;
                        grid-template-columns: repeat(3, 1fr);      
                        
                        > li {
                            > a {
                                display: flex;
                                align-items: center;
                                color: #fff;
                                padding: 1rem 0;

                                &::before {
                                    @include dot(.5rem, $blue);
                                }

                            }
                        }
                    }
                }
            }
        }

        .nav__submenu {
            position: absolute;
            background: $navy;
            width: 100%;
            left: 0;
            z-index: 99;   
            padding: 2rem 0;
            top: 4rem; //align with header-top height

        }
        .item--has-submenu {
            &.menu--open {
                > a {
                    text-decoration: underline;
                }
            }
        }
        .item--contact {
            display: none;
        }

    }

    @media (max-width: $breakpoint_desktop) and (min-width: $breakpoint_mobile + 1px) {
        a {
            font-size: 1em;
        }
    }

    @media (max-width: $breakpoint_mobile) {

        text-align: center;

        &:not(.menu--open) {
            display: none;
        }

        background: $navy;

        a {
            font-size: 1.125em;
            padding: 1rem;
            color: #fff;
        }

        > .nav__list {
            > li {
                border-bottom: 1px solid #2D2C3B;
            }
        }
        .nav__submenu {
            background: #2E2C3E;
            a {
                font-size: 1em;
            }
            li {
                &:not(:last-child) {
                    border-bottom: 1px solid #3C3A4B;
                }
            }
        }

    }

}