body {
    &.modal--active {
        //disable scroll, but don't hide scrollbar to avoid horizontal jump
        position: fixed; 
        overflow-y: scroll;
        width: 100%;
    }
}
.modal {
    
    //background: rgba(0, 0, 0, .9);

    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 999;

    .ui-close {
        display: block;

        position: absolute;
        z-index: 20; //above video player (see components/video.scss)
        top: 2rem;
        right: 2rem;

        color: #fff;

        text-align: center;

        .ui-ico {
            margin: 0;
            height: 2rem;
        }
        
        .label {
            display: block;
            font-size: .75em;
            text-transform: uppercase;
            margin-top: .25rem;
            letter-spacing: .1em;
        }
    }
    
    &:not(.modal--open) {
        display: none;
    }

    .modal__overlay {
        display: flex;
        align-items: center;
        background: rgba(0, 0, 0, .88);
        backdrop-filter: blur(.5rem);
    }

    .modal__body {
        width: 100%;
        max-width: 1440px;
        margin: 0 auto;
        padding: 1rem;
    }

    //override for default see components/video.scss
    .video-component {
        height: auto;
        padding-bottom: 56.25%;
    }

    @media (max-width: $breakpoint_mobile) {
        .ui-close {
            top: 1rem;
            right: 1rem;
        }
        .modal__body {
            padding: 1rem .5rem;
        }
    }
}