.header-top {
    z-index: 99;
    background: #fff;   


    @media (min-width: $breakpoint_mobile + 1px) {

        position: fixed;
        width: 100%;
        top: 0;
        box-shadow: 0 0 3rem rgba(0, 0, 0, .25);


        .header__layout {
            display: grid;
            grid-template-columns: 1fr;
            grid-auto-flow: column;
            align-items: center;
            height: 4rem;
        }
        .header__nav {
            .block-social {
                display: none;
            }
        }

    }

    @media (max-width: $breakpoint_desktop) {
        .header__cta {
            .button {
                margin-right: 0;
            }
            .block-social {
                display: none;
            }
        }
    }

    @media (max-width: $breakpoint_mobile) {

        .viewport {
            padding: 0;
        }
        .header__cta {
            display: none;
        }

        .header__branding {
            display: flex;
            justify-content: space-between;
            
            align-items: center;   
            padding: 1rem;         
        }

    }

}

.header__cta {

    display: grid;
    grid-auto-flow: column;
    align-items: center;
    
    .button {
        font-size: 1em;
        min-height: 0;
        min-width: 0;
        margin: 0 1rem 0;
        padding: .5rem 2rem;
    }
}