* {
  padding: 0;
  margin: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.logo {
  display: block;
}
.logo svg {
  width: auto;
  height: 2rem;
  vertical-align: bottom;
}
.logo.logo--white .svg__secondary {
  fill: #fff;
}

.embed-container {
  position: relative;
  padding-bottom: 56.25%;
}
.embed-container iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.overlay {
  position: fixed;
  z-index: 666;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
}
@media (max-width: 960px) {
  .overlay.overlay--sidebar {
    display: none;
  }
}

body {
  font-family: "Inter", sans-serif;
  font-size: 1em;
  font-weight: 300;
  color: #777F85;
  overflow-x: hidden;
}

a {
  color: #272727;
  text-decoration: none;
}
@media (hover: hover) {
  a:hover {
    text-decoration: underline;
  }
}

ul {
  list-style-type: none;
}

img {
  width: 100%;
  height: auto;
  vertical-align: bottom;
}

address {
  font-style: normal;
}

button {
  cursor: pointer;
  background: transparent;
}

figcaption {
  margin-top: 0.5rem;
  font-style: italic;
  color: silver;
}

dl dt {
  float: left;
  margin-right: 1rem;
}

.viewport {
  width: 100%;
  max-width: 1650px;
  margin: 0 auto;
  padding-left: 1rem;
  padding-right: 1rem;
}
.viewport.viewport--article {
  max-width: 960px;
}
.viewport.viewport--tablet {
  max-width: 1024px;
}
.viewport.viewport--desktop {
  max-width: 1180px;
}
.viewport.viewport--hd {
  max-width: 1280px;
}
.viewport.viewport--laptop {
  max-width: 1440px;
}
.viewport.viewport--no-padding {
  padding: 0;
}
.viewport .viewport {
  padding: 0;
}
@media all and (max-width: 960px) {
  .viewport {
    padding-left: 0.5rem;
    padding-right: 0.5rem;
  }
}

.text--rich h1,
.text--rich h2 {
  font-size: 3em;
  max-width: 60rem;
  margin: 2rem 0;
}
@media all and (max-width: 960px) {
  .text--rich h1,
.text--rich h2 {
    font-size: 2em;
  }
}
.text--rich h3 {
  font-size: 2em;
  font-weight: 500;
}
@media (max-width: 960px) {
  .text--rich h3 {
    font-size: 1.75em;
  }
  .text--rich h3:not(.header--no-margin) {
    margin-bottom: 1rem;
  }
}
.text--rich blockquote {
  color: #272727;
  margin: 2rem 0;
  border-left: 0.25rem solid #0045E6;
  padding-left: 2rem;
}
.text--rich blockquote p {
  font-weight: 300;
  font-size: 1.5em;
}
@media (max-width: 960px) {
  .text--rich blockquote {
    padding-left: 1em;
  }
  .text--rich blockquote p {
    font-size: 1.25em;
  }
}
.text--rich ul,
.text--rich ol {
  padding-left: 3rem;
  margin-bottom: 2rem;
}
@media all and (max-width: 960px) {
  .text--rich ul,
.text--rich ol {
    padding-left: 2rem;
  }
}
.text--rich ol {
  list-style-type: decimal;
}
.text--rich ul > li {
  vertical-align: middle;
}
.text--rich ul > li:before {
  content: "";
  display: inline-block;
  width: 0;
  height: 0;
  border-top: 4px solid transparent;
  border-bottom: 4px solid transparent;
  border-left: 4px solid #0045E6;
  position: relative;
  margin-right: 0.5rem;
  top: -2px;
}
.text--rich a:not(.button) {
  color: #272727;
  text-decoration: underline;
}
@media (hover: hover) {
  .text--rich a:not(.button):hover {
    color: #272727;
  }
}
.text--rich p,
.text--rich li {
  font-weight: 300;
  line-height: 2;
  margin-bottom: 1rem;
}
@media (min-width: 961px) {
  .text--rich p,
.text--rich li {
    font-size: 1.125em;
  }
}
.text--rich p strong,
.text--rich p b,
.text--rich li strong,
.text--rich li b {
  color: #272727;
  font-weight: 600;
}
.text--rich li:not(:last-child) {
  margin-bottom: 0.5rem;
}
.text--rich figure:not(.ui-ico) {
  width: 100% !important;
}
.text--rich figure img {
  margin-bottom: 0;
}
.text--rich figure figcaption {
  font-size: 0.875em;
  color: #A3A8AC;
  margin: 0.5rem 0 1rem;
}
.text--rich img {
  width: 100%;
  height: auto;
  max-width: 100%;
  margin-bottom: 1rem;
}

.text--center {
  text-align: center;
}

.text--white,
.text--white * {
  color: #fff;
}
.text--white strong,
.text--white a,
.text--white * strong,
.text--white * a {
  color: #fff;
}

h1,
h2,
h3,
h4 {
  font-family: "Silka", sans-serif;
  font-weight: 400;
  color: #28234A;
}
h1:not(.header--no-margin),
h2:not(.header--no-margin),
h3:not(.header--no-margin),
h4:not(.header--no-margin) {
  margin-bottom: 2rem;
}

h1 {
  font-size: 2em;
}

h2 {
  font-size: 1.5em;
}

h3 {
  font-size: 1.375em;
}

h4 {
  font-size: 1.25em;
}

p {
  line-height: 2;
  margin-bottom: 1rem;
}

/* UI (User Intefrace) */
/**
 * Hamburger to x transformicon source:
 * https://codepen.io/designcouch/pen/Atyop 
 **/
.ui-nav {
  display: block;
  position: relative;
  width: 3rem;
  height: 30px;
  text-indent: -999em;
  cursor: pointer;
}
.ui-nav span {
  display: block;
  position: absolute;
  height: 5px;
  width: 100%;
  background: #272727;
  opacity: 1;
  left: 0;
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: 0.25s ease-in-out;
  transition: 0.25s ease-in-out;
}
.ui-nav span:nth-child(1) {
  top: 0px;
}
.ui-nav span:nth-child(2), .ui-nav span:nth-child(3) {
  top: 12px;
}
.ui-nav span:nth-child(4) {
  top: 24px;
}
.ui-nav.menu--open span:nth-child(1) {
  top: 10px;
  width: 0%;
  left: 50%;
}
.ui-nav.menu--open span:nth-child(2) {
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}
.ui-nav.menu--open span:nth-child(3) {
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}
.ui-nav.menu--open span:nth-child(4) {
  top: 10px;
  width: 0%;
  left: 50%;
}
@media (min-width: 961px) {
  .ui-nav {
    display: none;
  }
}

.ui-list li:not(:last-child) {
  margin-bottom: 1rem;
}
.ui-list a {
  display: inline-flex;
  align-items: center;
  font-family: "Silka", sans-serif;
  font-size: 1.125em;
  font-weight: 600;
}

.ui-ico {
  display: inline-flex;
  align-items: center;
  width: auto;
  height: 3rem;
  margin-right: 0.5rem;
}
.ui-ico img,
.ui-ico svg {
  width: auto;
  height: 100%;
}
.ui-ico .svg__ico {
  transition: all 0.2s ease-in-out;
}
.ui-ico.ico--arrow {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  justify-content: center;
  background: #DEE2ED;
}
.ui-ico.ico--arrow svg {
  width: 0.25rem;
}

.ui-slider a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  background: #EFEFF8;
  text-decoration: none;
  cursor: pointer;
}
.ui-slider a:not(:last-child) {
  margin-bottom: 1rem;
}
.ui-slider a[aria-disabled=true] {
  opacity: 0.25;
}
.ui-slider .ui-ico {
  width: 1rem;
  height: 1rem;
  margin: 0;
}
.ui-slider .ui__next .ui-ico {
  transform: rotate(-180deg);
}

/**
 *  Warning: we can't use slash as a division anymore - use sass:math instead, see:
 *  https://sass-lang.com/documentation/breaking-changes/slash-div
 */
.grid--viewport {
  display: grid;
  grid-template-columns: 1fr 1fr [viewport-start] repeat(12, 8.33333%) [viewport-end] 1fr 1fr;
  grid-template-rows: auto;
}
@media all and (min-width: 1651px) {
  .grid--viewport {
    grid-template-columns: 1fr 1fr [viewport-start] repeat(12, minmax(auto, 137.5px)) [viewport-end] 1fr 1fr;
  }
}

.grid--content {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
}

.grid--two-columns {
  display: grid;
  grid-row-gap: 2rem;
}
@media all and (min-width: 961px) {
  .grid--two-columns {
    grid-template-columns: 1fr 1fr;
    grid-gap: 3rem 2rem;
  }
}

.grid--three-columns {
  display: grid;
  grid-row-gap: 1rem;
}
@media all and (min-width: 961px) {
  .grid--three-columns {
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 2rem 2rem;
  }
}

@media (min-width: 961px) {
  .page-container {
    margin-top: 4rem;
  }
}

.header-top {
