}
@media (max-width: 1180px) {
  .header-top .header__cta .button {
    margin-right: 0;
  }
  .header-top .header__cta .block-social {
    display: none;
  }
}
@media (max-width: 960px) {
  .header-top .viewport {
    padding: 0;
  }
  .header-top .header__cta {
    display: none;
  }
  .header-top .header__branding {
    display: flex;
    justify-content: space-between;
    align-items: center;
