    margin: 2rem 0;
  }
}

.section__header h2 {
  font-size: 3em;
  max-width: 45rem;
}
.section__header h2:last-child {
  margin: 0;
}
.section__header h3 {
  font-size: 1.5em;
  font-weight: 500;
  margin-bottom: 1rem;
}
.section__header p {
  font-size: 1.125em;
  line-height: 2;
  max-width: 40rem;
}
.section__header p:not(:last-child) {
  margin-bottom: 2rem;
}
.section__header p a {
  text-decoration: underline;
}
.section__header.text--center h2 {
