.button {
  font-weight: bold;
  font-size: 1.125em;
  min-height: 4rem;
  min-width: 16rem;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  background: #04E48C;
  color: #fff;
  border-radius: 2rem;
  margin: 1rem 0;
  padding: 0 3rem;
}
.button.button--white {
  background: #fff;
  color: #1E1D28;
}
.button.button--white .svg__ico {
  fill: #1E1D28;
}
.button.button--blue {
  background: #0045E6;
  color: #fff;
}
.button.button--silent {
  background: transparent;
  padding: 0;
  min-height: 0;
  min-width: 0;
  color: #272727;
  text-transform: uppercase;
  font-size: 1em;
}
.button.button--silent .button__ico {
  margin: 0;
}
.button.button--silent .button__label {
  margin-right: 0.5rem;
}
.button.button--silent.button--white {
  color: #fff;
}
.button .button__ico {
  margin: 0 0 0 0.5rem;
}
.button .button__ico svg {
  height: 1rem;
}
.button .button__ico .svg__ico {
  fill: #fff;
}
@media (hover: hover) {
  .button:hover {
    text-decoration: underline;
  }
}

.breadcrumbs {
  margin: 1rem 0;
}
@media all and (max-width: 960px) {
  .breadcrumbs {
    display: none;
  }
}

.breadcrumbs__list li {
  display: inline-block;
  font-size: 0.875em;
}
.breadcrumbs__list li a {
  display: inline-block;
  color: #999;
}
.breadcrumbs__list li a:after {
  display: none;
}
.breadcrumbs__list li:not(:last-child):after {
  content: "/";
  display: inline-block;
  margin: 0 0.5rem;
  color: #777F85;
}
.breadcrumbs__list li.item--start {
  margin-right: 0.5rem;
}
.breadcrumbs__list li.item--start:after {
  display: none;
}
.breadcrumbs__list li:last-child a {
  pointer-events: none;
}
.breadcrumbs__list li:last-child a:after {
  display: none;
}

.cover.cover--overlay::after, .cover img,
.cover video, .cover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.cover {
  overflow: hidden;
  z-index: 1;
}
.cover video {
  z-index: 2;
}
.cover img,
.cover video {
  object-fit: cover;
}
.cover img {
  object-position: top;
}
.cover.cover--overlay::after {
  content: "";
  display: block;
  background: rgba(0, 0, 0, 0.88);
  z-index: 3;
}
.cover.cover--overlay.overlay--blue:after {
  background: rgba(0, 69, 230, 0.8);
}
.cover.cover--overlay.overlay--blue img,
.cover.cover--overlay.overlay--blue video {
  filter: grayscale(100%);
}

/** Top Navigation embedded in views/layout/header; markup generated via wp_nav **/
.nav--top a {
  display: block;
  font-weight: 600;
  font-size: 1.125em;
}
.nav--top ul {
  display: none;
}
.nav--top > ul {
  display: block;
}
.nav--top .item--has-submenu > a {
  display: flex;
  align-items: center;
  justify-content: center;
}
.nav--top .item--has-submenu > a:after {
  margin-left: 0.5rem;
  content: "";
  display: block;
  width: 0;
  height: 0;
  border-left: 0.25rem solid transparent;
  border-right: 0.25rem solid transparent;
  border-top: 0.25rem solid #aaa;
  transition: all 0.2s ease-in-out;
  position: relative;
  margin-left: 0.5rem;
}
.nav--top .item--has-submenu.menu--open > a:after {
  transform: rotate(180deg);
}
.nav--top .item--has-submenu.menu--open .nav__submenu {
  display: block;
}
@media (min-width: 961px) {
  .nav--top > ul > li {
    display: inline-block;
    margin: 0 0.5rem;
  }
}
@media (min-width: 961px) and (max-width: 1180px) {
  .nav--top > ul > li {
    margin: 0 0.25rem;
  }
}
@media (min-width: 961px) {
  .nav--top > ul > li > a {
    padding: 1rem 0;
  }
}
@media (min-width: 961px) {
  .nav--top > ul > li > ul > .submenu__layout {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
  .nav--top > ul > li > ul > .submenu__layout > li > a {
    display: flex;
    align-items: center;
    color: #fff;
    padding: 1rem 0;
  }
  .nav--top > ul > li > ul > .submenu__layout > li > a::before {
    content: "";
    display: block;
    width: 0.5rem;
    height: 0.5rem;
    border-radius: 50%;
    background: #0045E6;
    margin-right: 1rem;
    flex-shrink: 0;
  }
}
@media (min-width: 961px) {
  .nav--top .nav__submenu {
    position: absolute;
    background: #1E1D28;
    width: 100%;
    left: 0;
    z-index: 99;
    padding: 2rem 0;
    top: 4rem;
  }
}
@media (min-width: 961px) {
  .nav--top .item--has-submenu.menu--open > a {
    text-decoration: underline;
  }
}
@media (min-width: 961px) {
  .nav--top .item--contact {
    display: none;
  }
}
@media (max-width: 1180px) and (min-width: 961px) {
  .nav--top a {
    font-size: 1em;
  }
}
@media (max-width: 960px) {
  .nav--top {
    text-align: center;
    background: #1E1D28;
  }
  .nav--top:not(.menu--open) {
    display: none;
  }
  .nav--top a {
    font-size: 1.125em;
    padding: 1rem;
    color: #fff;
  }
  .nav--top > .nav__list > li {
    border-bottom: 1px solid #2D2C3B;
  }
  .nav--top .nav__submenu {
    background: #2E2C3E;
  }
  .nav--top .nav__submenu a {
    font-size: 1em;
  }
  .nav--top .nav__submenu li:not(:last-child) {
    border-bottom: 1px solid #3C3A4B;
  }
}

