}
.video-component .video__container {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.video-component .video__poster {
  z-index: 2;
  text-align: center;
  transition: opacity 0.2s;
}
.video-component .video__desc {
  width: 100%;
  display: flex;
  align-items: center;
  padding: 2rem 1rem;
  position: relative;
  z-index: 4;
}
.video-component .video__button {
  margin-bottom: 0;
}
.video-component .button__label {
  display: block;
  color: #fff;
  font-size: 2em;
  font-weight: 400;
  max-width: 30rem;
  margin: 0 auto 1rem;
}
@media (min-width: 961px) {
  .video-component {
    height: 36rem;
    will-change: height;
    transition: height 0.4s ease-in-out;
  }
}
@media (max-width: 960px) {
  .video-component {
    padding-bottom: 56.25%;
  }
  .video-component .video__poster {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align: center;
  }
  .video-component .button__label {
    font-size: 2em;
  }
  .video-component .video__index {
    display: none;
  }
}
@media (max-width: 640px) {
  .video-component .button__label {
    font-size: 1.375em;
  }
}
@media (min-width: 960px) {
  .video-component[data-state=started], .video-component[data-state=playing] {
    height: 90vh;
  }
}
.video-component[data-state=started] .video__poster, .video-component[data-state=playing] .video__poster {
  opacity: 0;
}
.video-component[data-state=started] .video__container, .video-component[data-state=playing] .video__container {
  z-index: 10;
}
.video__button {
  display: inline-flex;
  align-items: center;
  text-align: left;
  margin-bottom: 2rem;
  color: #fff;
}
.video__button .ui-ico {
  height: 4rem;
  margin-right: 1.5rem;
  transition: transform 0.2s;
}
.video__button p {
  font-size: 1.25em;
  font-weight: bold;
  margin-bottom: 0.5rem;
  line-height: 1;
}
.video__button span {
  font-size: 1em;
}
@media (hover: hover) {
  .video__button:hover .ui-ico {
    transform: scale(1.1);
  }
}

.ui-tabs .tabs__nav .nav__item.tab--active {
  font-weight: bold;
}
@media all and (max-width: 960px) {
  .ui-tabs .tabs__nav {
    display: none;
  }
}
@media all and (min-width: 961px) {
  .ui-tabs .tabs__list .item--tab:not(.tab--active) {
    display: none;
  }
  .ui-tabs .tabs__list .item--tab .tab__title {
    display: none;
  }
}
@media all and (max-width: 960px) {
  .ui-tabs .tabs__list .item--tab.tab--active .tab__title .title__label {
    text-decoration: underline;
  }
  .ui-tabs .tabs__list .item--tab:not(.tab--active) .tab__content {
    display: none;
  }
  .ui-tabs .tabs__list .tab__title {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .ui-tabs .tabs__list .tab__title .title__label {
    margin-right: 1rem;
  }
  .ui-tabs .tabs__list .tab__title .title__ui {
    font-size: 0.875em;
  }
}
.ui-tabs .item--tab.tab--active .title__ui .ui--open {
  display: none;
}
.ui-tabs .item--tab:not(.tab--active) .title__ui .ui--close {
  display: none;
}

body.modal--active {
  position: fixed;
  overflow-y: scroll;
  width: 100%;
}

.modal {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 999;
}
.modal .ui-close {
  display: block;
  position: absolute;
  z-index: 20;
  top: 2rem;
  right: 2rem;
  color: #fff;
  text-align: center;
}
.modal .ui-close .ui-ico {
  margin: 0;
  height: 2rem;
}
.modal .ui-close .label {
  display: block;
  font-size: 0.75em;
  text-transform: uppercase;
  margin-top: 0.25rem;
  letter-spacing: 0.1em;
}
.modal:not(.modal--open) {
  display: none;
}
.modal .modal__overlay {
  display: flex;
  align-items: center;
  background: rgba(0, 0, 0, 0.88);
  backdrop-filter: blur(0.5rem);
}
.modal .modal__body {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 1rem;
}
.modal .video-component {
  height: auto;
  padding-bottom: 56.25%;
}
@media (max-width: 960px) {
  .modal .ui-close {
    top: 1rem;
    right: 1rem;
  }
  .modal .modal__body {
    padding: 1rem 0.5rem;
  }
}