.ui-tabs {
    .tabs__nav {

        ul {

        }

        .nav__item {


            &.tab--active {
                font-weight: bold;
            }

        }
        

        @media all and (max-width: $breakpoint_mobile) {
            display: none;
        }
    }

    //contents: tabs change to accordion on mobile
    .tabs__list {

        @media all and (min-width: $breakpoint_mobile + 1px) {
            .item--tab {
                &:not(.tab--active) {
                    display: none;
                }
                .tab__title {
                    display: none;
                }
            }
        }
        @media all and (max-width: $breakpoint_mobile) {

            .item--tab {
                
                &.tab--active {
                    .tab__title {
                        .title__label {
                            text-decoration: underline;
                        }
                    }
                }
                &:not(.tab--active) {
                    .tab__content {
                        display: none;
                    }
                }
            }

            .tab__title {
                display: flex;
                justify-content: space-between;
                align-items: center;

                .title__label {
                    margin-right: 1rem;
                }
                .title__ui {
                    font-size: .875em;
                }
            }

        }

    }   

    .item--tab {
        &.tab--active {
            .title__ui {
                .ui--open {
                    display: none;
                }
            }
        }
        &:not(.tab--active) {
            .title__ui {
                .ui--close {
                    display: none;
                }
            }            
        }        
    }


}