/* UI (User Intefrace) */

/**
 * Hamburger to x transformicon source:
 * https://codepen.io/designcouch/pen/Atyop 
 **/

.ui-nav {
    display: block;
    position: relative;
    width: 3rem;
    height: 30px;

    text-indent: -999em;
    
    cursor: pointer;

    span {
        display: block;
        position: absolute;
        height: 5px;
        width: 100%;
        background: #272727;
        opacity: 1;
        left: 0;

        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
        -webkit-transition: .25s ease-in-out;
                transition: .25s ease-in-out;       
                        
        &:nth-child(1) {
            top: 0px;
        }  
        &:nth-child(2),
        &:nth-child(3) {
            top: 12px;
        }
        &:nth-child(4) {
            top: 24px;
        }
    }

    
    &.menu--open {
        span {
            &:nth-child(1) {
                    top: 10px;
                    width: 0%;
                    left: 50%;
            }
            &:nth-child(2) {
                    -webkit-transform: rotate(45deg);
                            transform: rotate(45deg);               
            }
            &:nth-child(3) {
                    -webkit-transform: rotate(-45deg);
                            transform: rotate(-45deg);               
            }
            &:nth-child(4) {
                    top: 10px;
                    width: 0%;
                    left: 50%;               
            }
        }
        
    }

    @media (min-width: $breakpoint_mobile + 1px) {
        display: none;
    }
}


.ui-list {
    li {
        &:not(:last-child) {
            margin-bottom: 1rem;
        }
    }


    a {
        display: inline-flex;
        align-items: center;

        font-family: 'Silka', sans-serif;
        font-size: 1.125em;
        font-weight: 600;
    }
}

//inline svgs icons
.ui-ico {

    display: inline-flex;
    align-items: center;
    width: auto;
    height: 3rem;
    margin-right: .5rem;

    img,
    svg {
        width: auto;
        height: 100%;
    }

    //paths, polygons, lines, rects etc controlled by additional class
    .svg__ico {			
        transition: all .2s ease-in-out;
    }

    &.ico--arrow {
        width: 1rem;
        height: 1rem;
        border-radius: 50%;
        justify-content: center;

        background: #DEE2ED;
        svg {
            width: .25rem;
        }        
    }

}

.ui-slider {
    a {
        display: flex;
        justify-content: center;
        align-items: center;

        width: 3rem;
        height: 3rem;
        border-radius: 50%;
        background: $silver;
        text-decoration: none;

        cursor: pointer;

        &:not(:last-child) {
            margin-bottom: 1rem;
        }

        &[aria-disabled='true'] {
            opacity: .25;
        }        

    }

    .ui-ico {
        width: 1rem;
        height: 1rem;
        margin: 0;
    }

    .ui__next {
        .ui-ico {
            transform: rotate(-180deg);
        }
    }
}