.banner {
    position: relative;
    display: grid;
    align-content: center;
    background: $navy;
    min-height: 30rem;
    padding: 4rem 0;

    h1 {
        font-size: 4em;
        font-weight: 600;
        max-width: 60rem;
        line-height: 1;
    }

    .banner__body {
        position: relative;
        z-index: 2; //above cover

        p {
            font-size: 1.125em;
            max-width: 60rem;
            margin: 2rem 0;
        }

        .button {
            margin: 0;
        }

    }

    @media (max-width: $breakpoint_mobile) {
        margin-bottom: 2rem;

        h1 {
            font-size: 2.5em;
        }
        .banner__body {
            .button {
                width: 100%;
            }
        }        
    }    
    @media (max-width: $breakpoint_phone) {
        h1 {
            font-size: 2em;
        }

    }


}