.button {
    font-weight: bold;
    font-size: 1.125em;

    min-height: 4rem;
    min-width: 16rem;

    display: inline-flex;
    justify-content: center;
    align-items: center;

    background: $green;
    color: #fff;

    border-radius: 2rem;
    margin: 1rem 0;
    padding: 0 3rem;


    &.button--white {
        background: #fff;
        color: $navy;

        .svg__ico {
            fill: $navy;
        }
    }
    &.button--blue {
        background: $blue;
        color: #fff;
    }

    &.button--silent {
        background: transparent;
        padding: 0;
        min-height: 0;
        min-width: 0;

        color: #272727;

        text-transform: uppercase;
        font-size: 1em;

        .button__ico {
            margin: 0;
        }
        .button__label {
            margin-right: .5rem;
        }

        &.button--white {
            color: #fff;
        }
    }

    .button__ico {
        margin: 0 0 0 .5rem;
        svg {
            height: 1rem;
            
        }
        .svg__ico {
            fill: #fff;
        }
    }


    
    @media (hover: hover) {
        &:hover {
            text-decoration: underline;
        }
    }

}