h1,
h2,
h3,
h4 {
    
    font-family: 'Silka', sans-serif;
    font-weight: 400;
    color: #28234A;

    &:not(.header--no-margin) {
        margin-bottom: 2rem;
    }
}

h1 {
    font-size: 2em;
}
h2 {
    font-size: 1.5em;
}
h3 {
    font-size: 1.375em;
}
h4 {
    font-size: 1.25em;
}

p {
    line-height: 2;
    margin-bottom: 1rem;
}